.inputWithIcon input[type="text"],input[type="number"]  {
   width: 100%;
   border: 1px solid #aaa;
   border-radius: 4px;
   margin: 8px 0;
   outline: none;
   padding: 8px;
   box-sizing: border-box;
   -webkit-transition: 0.3s;
   transition: 0.3s;
   font-family: 'Vazir';
   font-weight: 400;
   text-align: right;
   direction: rtl;
 }
 
 .inputWithIcon input[type="text" ]:focus, input[type="number" ]:focus {
   border-color: #b71c1c;
   box-shadow: 0 0 8px 0 #b71c1c;
 }

 .inputWithIcon input[type="text"], .inputWithIcon input[type="number"]{
   padding-right: 40px;
 }
 
 .inputWithIcon {
   position: relative;
 }
 
 .inputWithIcon i {
   position: absolute;
   right: 0;
   top: 8px;
   padding: 9px 8px;
   color: #aaa;
   -webkit-transition: 0.3s;
   transition: 0.3s;
 }
 
 .inputWithIcon input[type="text"]:focus + i , .inputWithIcon input[type="number"]:focus + i {
   color: #b71c1c;
 }
 
 .inputWithIcon.inputIconBg i {
   background-color: #aaa;
   color: #fff;
   padding: 9px 4px;
   border-radius: 4px 0 0 4px;
 }
 
 .inputWithIcon.inputIconBg input[type="text"]:focus + i, .inputWithIcon.inputIconBg input[type="number"]:focus + i {
   color: #fff;
   background-color: #b71c1c;
 }
 







