@media only screen and (min-width : 220px) {

    .inputGroup label {
        font-size: 8px;
    }



}



/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {

    .inputGroup {

        width: 324px;

    }

    .inputGroup label {
        font-size: 10px;
        width: 100%;
    }


    .inputGroup label:before {

        -webkit-transition: all 360ms cubic-bezier(0, 0.0, 0.2, 1);

        transition: all 360ms cubic-bezier(0, 0.0, 0.2, 1);

    }

    .inputGroup label:after {
        width: 20px;
        height: 20px;
        border: 1px solid #D1D7DC;
