.circle__box {
    width: 200px;
    height: 200px;
    margin: 50px auto;
    position: relative;
    
    
}

.circle__wrapper {
    width: 100px;
    height: 200px;
    position: absolute;
    top: 0;
    overflow: hidden;
}

.circle__wrapper--right {
    right: 0;
}

.circle__wrapper--left {
    left: 0;
}

.circle__whole {
    width: 160px;
    height: 160px;
    border: 20px solid transparent;
    border-radius: 50%;
    position: absolute;
    top: 0;
    -webkit-transform: rotate(-135deg);
            transform: rotate(-135deg);
}

.circle__right {
    border-top: 20px solid #00c853;
    border-right: 20px solid #00c853;
    right: 1px;
    -webkit-animation: circleRight 800ms linear forwards;
            animation: circleRight 800ms linear forwards;
}

.circle__left {
    border-bottom: 20px solid #00c853;
    border-left: 20px solid #00c853;
    left: 1px;
    -webkit-animation: circleLeft 800ms linear forwards;
            animation: circleLeft 800ms linear forwards;
}

@-webkit-keyframes circleRight {
    0% {
        -webkit-transform: rotate(-135deg);
                transform: rotate(-135deg);
    }

    50%,
    100% {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }
}

@keyframes circleRight {
    0% {
        -webkit-transform: rotate(-135deg);
                transform: rotate(-135deg);
    }

    50%,
    100% {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }
}

@-webkit-keyframes circleLeft {

    0%,
    50% {
        -webkit-transform: rotate(-135deg);
                transform: rotate(-135deg);
    }

    100% {
        -webkit-transform: rotate(45deg);
    }
}

@keyframes circleLeft {

    0%,
    50% {
        -webkit-transform: rotate(-135deg);
                transform: rotate(-135deg);
    }

    100% {
        -webkit-transform: rotate(45deg);
    }
}
