@import url('//maxcdn.bootstrapcdn.com/font-awesome/4.3.0/css/font-awesome.min.css');

.row {
    width: 100%;
    justify-content: flex-start;
    display: flex;
    flex-flow: row wrap;
}




#social {
    margin: 6px 6px;
    text-align: center;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-content: space-between;
pointer-events: none;
}

.smGlobalBtn {
    /* global button class */
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    position: relative;
    cursor: pointer;
    width: 28px;
    height: 28px;
    box-shadow: 0 2px 2px #999;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    text-align: center;
    margin: 4px;
    color: #fff;
    font-size: 18px;
    font-weight: normal;

    border-radius: 25px;
    -moz-border-radius: 25px;
    -webkit-border-radius: 25px;

}

/* facebook button class*/
.facebookBtn {
    background: #4060A5;
}

.facebookBtn:before {
    /* use :before to add the relevant icons */
    font-family: "FontAwesome";
    content: "\f09a";
    /* add facebook icon */
}

.facebookBtn:hover {
    color: #4060A5;
    background: #fff;
}

